@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: 'Open Sans', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.landing-box {
	display: flex;
	justify-content: center;
	flex-direction: column;
	margin-top: 15em;
}

.landing-header h1 {
	font-weight: 900;
	font-size: 4.5em;
	margin: 0;
	padding: 0;
}

.landing-header {
	display: flex;
	justify-content: center;
	flex-direction: row;
	flex-flow: row wrap;
	align-items: center;
}

.landing-input-box input {
	padding: 10px 50px 10px 25px;
	border-radius: 5px 0 0 5px;
	border: none;
	color: #fafafa;
	font-weight: 700;
	background-color: #0f0f0f;
	font-family: 'Open Sans', sans-serif;
	margin-top: 4em;
	width: 300px;
}

.landing-input-box {
	text-align: center;
}

.landing-input-box input::-webkit-input-placeholder {
	font-weight: 700;
	color: #858585;
	font-family: 'Open Sans', sans-serif;
}

.landing-input-box input::-moz-placeholder {
	font-weight: 700;
	color: #858585;
	font-family: 'Open Sans', sans-serif;
}

.landing-input-box input:-ms-input-placeholder {
	font-weight: 700;
	color: #858585;
	font-family: 'Open Sans', sans-serif;
}

.landing-input-box input::-ms-input-placeholder {
	font-weight: 700;
	color: #858585;
	font-family: 'Open Sans', sans-serif;
}

.landing-input-box input::placeholder {
	font-weight: 700;
	color: #858585;
	font-family: 'Open Sans', sans-serif;
}

.landing-input-box button {
	padding: 10px 20px 10px 20px;
	border-radius: 0 5px 5px 0;
	border: none;
	font-weight: 700;
	font-family: 'Open Sans', sans-serif;
}

.landing-input-box button:hover {
	cursor: pointer;
	background-color: #c0c0c0;
}

.corndel-logo {
	padding: 0;
	width: 300px;
	margin-left: 10px;
}
